
import {Component, ViewChild} from '@angular/core';
import {Nav, Platform, MenuController, AlertController, ToastController} from 'ionic-angular';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import { jmyapis } from '../providers/jmyapis';
import { jmyfatkit } from '../providers/jmy-fat-kit';

export interface MenuItem {
    title: string;
    component: any;
    icon: string;
}

@Component({
    templateUrl: 'app.html'
})
export class foodIonicApp {
    @ViewChild(Nav) nav: Nav;

  	tabsPlacement: string = 'bottom';
  	tabsLayout: string = 'icon-top';
    rootPage: any = 'page-walkthrough';
    showMenu: boolean = true;
    homeItem: any;
    initialItem: any;
    messagesItem: any;
    settingsItem: any;
    appMenuItems: Array<MenuItem>;
    yourRestaurantMenuItems: Array<MenuItem>;
    accountMenuItems: Array<MenuItem>;
    helpMenuItems: Array<MenuItem>;

    constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen, public jmyApis: jmyapis,public jmyFatKit: jmyfatkit,public alertCtrl: AlertController,public toastCtrl: ToastController, public menu: MenuController) {
        this.initializeApp();

        this.homeItem = { component: 'page-home' };
        this.messagesItem = { component: 'page-message-list'};


        this.appMenuItems = [
            {title: 'Clinicas', component: 'page-restaurant-list', icon: 'home'},
            {title: 'Recetas', component: 'page-dish-list', icon: 'pizza'},
            /*{title: '', component: 'page-nearby', icon: 'compass'},
            {title: '', component: 'page-category', icon: 'albums'},
            {title: '', component: 'page-orders', icon: 'list-box'},
            {title: '', component: 'page-cart', icon: 'cart'},
			{title: '', component: 'page-favorite-list', icon: 'heart'}*/
        ];


        this.accountMenuItems = [
            {title: 'Entrar', component: 'page-auth', icon: 'log-in'},
            {title: 'Mi perfil', component: 'page-my-account', icon: 'contact'},
            {title: 'Salir', component: 'page-auth', icon: 'log-out'},
        ];

        this.helpMenuItems = [
            {title: 'Acerca', component: 'page-about', icon: 'information-circle'},
            {title: 'Ayuda', component: 'page-support', icon: 'call'},
            {title: 'Configuración de la app', component: 'page-settings', icon: 'cog'}
        ];

    }

    initializeApp() {
        this.platform.ready().then(() => {
            this.statusBar.overlaysWebView(false);
            this.splashScreen.hide();
        });

	    if (!this.platform.is('mobile')) {
	      this.tabsPlacement = 'top';
	      this.tabsLayout = 'icon-left';
	    }
    }
salirConfirmar() {
    let confirm = this.alertCtrl.create({
      title: 'Estas seguro',
      message: '¿que ya deseas salir?',  
      buttons: [{
          text: 'Regresar',
          handler: () => { }},{
          text: 'Salir',
          handler: () => {
            this.alerta("Hasta luego! ");
            this.logout();
          }}]});
    confirm.present();
    }
    logout(){
        localStorage.clear();


        this.menu.enable(false);
        this.openPage('page-auth');
        //location.reload();
    }
    nombreUsuario(){const d=JSON.parse(localStorage.getItem('userData'));
      if(d!=undefined){ return(this.jmyFatKit.user!=undefined)?this.jmyFatKit.user.nombre:d.userData.name; } }
   

    alerta(men) {
        const toast = this.toastCtrl.create({
          message: men,
          duration: 9000,
          position: 'top'
        });
        toast.present();
    }

    openPage(page) {
        this.nav.setRoot(page.component);
    }
    openPage(page) {
        // Reset the content nav to have just this page
        // we wouldn't want the back button to show in this scenario
        this.nav.setRoot(page.component);
    }
}



